import { useState, useEffect } from "react";
import "../css/Home.css";

import axios from "../../axios";

const Home = () => {
  const [Status, setStatus] = useState(false);
  const [HomeUser, setHomeUser] = useState();
  const HomePage = async () => {
    try {
      const res = await axios.get("/navbar");
      setHomeUser(res.data.username);
      setStatus(true);
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    HomePage();
  }, []);
  return (
    <>
      <div className="header">
        {Status === true && (
          <>
            <h2>Welcome Back</h2>
            <h1>{HomeUser}!</h1>
          </>
        )}
        {Status === false && (
          <>
            <h2>We're</h2>
            <h1>The MERN Developers</h1>
          </>
        )}
      </div>
    </>
  );
};

export default Home;
